// src/components/chatbot/MessageParser.js
class MessageParser {
    constructor(actionProvider) {
      this.actionProvider = actionProvider;
    }
  
    parse(message) {
      const lowerCaseMessage = message.toLowerCase();
  
      if (lowerCaseMessage.includes("servicio") || lowerCaseMessage.includes("ayuda")) {
        this.actionProvider.handleServices();
      } else if (lowerCaseMessage.includes("contacto") || lowerCaseMessage.includes("cita")) {
        this.actionProvider.handleContact();
      } else if (lowerCaseMessage.includes("precio") || lowerCaseMessage.includes("costo")) {
        this.actionProvider.handlePricing();
      } else {
        this.actionProvider.handleDefault();
      }
    }
  }
  
  export default MessageParser;