// src/components/chatbot/Options.js
import React from "react";
import { chatbotStyles } from '../../styles/sharedStyles';

const Options = (props) => {
  const options = [
    { text: "Servicios", handler: props.actionProvider.handleServices, id: 1 },
    { text: "Contacto", handler: props.actionProvider.handleContact, id: 2 },
    { text: "Precios", handler: props.actionProvider.handlePricing, id: 3 },
  ];

  const optionsMarkup = options.map((option) => (
    <button
      className={chatbotStyles.optionButton}
      key={option.id}
      onClick={option.handler}
    >
      {option.text}
    </button>
  ));

  return <div className={chatbotStyles.optionsContainer}>{optionsMarkup}</div>;
};

export default Options;