// src/styles/sharedStyles.js

import React from 'react';

export const colors = {
    primary: 'bg-blue-900 dark:bg-blue-800',
    secondary: 'bg-blue-800 dark:bg-blue-700',
    accent: 'text-blue-400 dark:text-blue-300',
    text: 'text-gray-800 dark:text-gray-200',
    background: 'bg-gray-100 dark:bg-gray-900',
    white: 'bg-white dark:bg-gray-800',
  };
  

export const typography = {
  sans: 'font-sans',
  serif: 'font-serif',
};

export const shadows = {
  sm: 'shadow-sm',
  md: 'shadow-md',
  lg: 'shadow-lg',
};

export const Button = ({ children, primary = false, className = '', ...props }) => (
  <button
    className={`px-6 py-2 rounded-md font-semibold transition duration-300 ${
      primary
        ? `${colors.primary} text-white hover:bg-blue-800`
        : `${colors.white} ${colors.text} border border-blue-900 hover:bg-blue-900 hover:text-white`
    } ${className}`}
    {...props}
  >
    {children}
  </button>
);

export const Card = ({ children, className = '', ...props }) => (
  <div
    className={`${colors.white} rounded-lg ${shadows.md} p-6 ${className}`}
    {...props}
  >
    {children}
  </div>
);

export const chatbotStyles = {
  widget: 'fixed bottom-5 right-5 z-50',
  container: 'w-72 h-96 absolute bottom-16 right-0 shadow-lg',
  toggler: `${colors.primary} text-white border-none py-2 px-4 rounded cursor-pointer`,
  optionsContainer: 'flex flex-wrap justify-center',
  optionButton: `m-1 py-2 px-4 ${colors.primary} text-white border-none rounded cursor-pointer hover:bg-blue-800 transition duration-300`,
};