// src/components/ChatbotWidget.js
import React, { useState } from 'react';
import Chatbot from 'react-chatbot-kit';
import 'react-chatbot-kit/build/main.css';
import config from './chatbot/config';
import MessageParser from './chatbot/MessageParser';
import ActionProvider from './chatbot/ActionProvider';
import { chatbotStyles } from '../styles/sharedStyles';

function ChatbotWidget() {
  const [showBot, setShowBot] = useState(false);

  const toggleBot = () => {
    setShowBot(!showBot);
  };

  return (
    <div className={chatbotStyles.widget}>
      {showBot && (
        <div className={chatbotStyles.container}>
          <Chatbot
            config={config}
            messageParser={MessageParser}
            actionProvider={ActionProvider}
          />
        </div>
      )}
      <button onClick={toggleBot} className={chatbotStyles.toggler}>
        {showBot ? 'Cerrar Chat' : 'Abrir Chat'}
      </button>
    </div>
  );
}

export default ChatbotWidget;