// src/components/chatbot/config.js
import { createChatBotMessage } from "react-chatbot-kit";
import Options from "./Options";

const config = {
  botName: "Asistente Legal",
  initialMessages: [
    createChatBotMessage("Hola! Soy el asistente virtual de Bufete Salas Granados. En qu puedo ayudarte hoy?", {
      widget: "options",
    }),
  ],
  widgets: [
    {
      widgetName: "options",
      widgetFunc: (props) => <Options {...props} />,
    },
  ],
};

export default config;